body {
  font-family: 'Roboto Mono', monospace;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.4;
  color: #434343; }

.text--regular {
  font-size: 16px; }

.text--small {
  font-size: 12px; }

.text--xsmall {
  font-size: 10px; }

.text--large {
  font-size: 45px; }

.text--xlarge {
  font-size: 55px; }

body {
  background-color: #434343;
  font-size: 16px; }

#root {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh; }

.content {
  width: 450px;
  max-width: 95%;
  padding: 30px 50px 0;
  background-color: #7d7d7f;
  margin: 0 auto;
  box-shadow: 0 0 5px 3px rgba(0, 0, 0, 0.75);
  border-radius: 5px;
  box-sizing: border-box; }

.align--left {
  text-align: left; }

.align--right {
  text-align: right; }

.align--center {
  text-align: center; }

.keyboard {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  flex-wrap: wrap;
  align-items: center;
  margin-top: 50px; }
  .keyboard__button {
    width: 26%;
    line-height: 0;
    margin-bottom: 15px;
    background-color: #63636e;
    color: #f3f3f3;
    font-weight: 500;
    border-radius: 5px;
    box-shadow: 0 0 8px 1px rgba(0, 0, 0, 0.75);
    cursor: pointer;
    padding: 0 5px;
    position: relative; }
  .keyboard__symbol {
    color: #434343;
    font-weight: 500;
    position: absolute;
    margin: 0;
    bottom: 12px;
    right: 12px; }

.display__wrapper {
  background-color: #47b2b2;
  width: 100%;
  border-radius: 5px;
  padding: 10px;
  box-sizing: border-box; }
  .display__wrapper--active {
    background-color: #7fffff; }

.display__input {
  display: block;
  width: 100%;
  background-color: transparent;
  border: 0;
  cursor: default;
  outline: none;
  color: #434343;
  font-family: 'Roboto Mono', monospace; }
  .display__input--bottom {
    padding: 50px 0 15px; }
